.news {
	&_right {
		width: 530px;
	}
	&_single:nth-last-child(1) {
		.news_shop {
			margin-bottom: 0;
		}
	}
	&_shop {
		overflow: hidden;
		display: block;
		padding-bottom: 28px;
		margin-bottom: 28px;
		border-bottom: $common-border;
	}
	&_shijian {
		@include size(80px,80px);
		padding-top: 12px;
		margin-right: 12px;
		background-color: $white-color;
	}
	&_wen {
		width: 438px;
		overflow: hidden;
		&_cont {
			margin-top: 8px;
			@include rowhide(2,24px);
			color: $nine-color;
		}
	}
	&_left {
		overflow: hidden;
		display: block;
		margin-right: 30px;
		@include size(580px,386px);
		@extend .bg_cover;
	}
	&_item {
		padding-bottom: 30px;
		margin-bottom: 30px;
		border-bottom: $common-border;
	}
	&_txt {
		width: 910px;
		padding: 20px 0;
		overflow: hidden;
	}
	&_desc {
		position: relative;
		margin: 12px 0;
		@include rowhide(4,20px);
		text-indent: 2em;
		text-align: justify;
		color: #8c8c8c;		
	}
	&_date {
		position: relative;
		margin-top: 30px;
		font-size: 12px;
		color: #b3b3b3;
		span {
			margin-right: 20px;
		}
		&:before {
			content: '';
			display: block;
			position: absolute;
			top: -14px;
			left: 0;
			@include size(40px,1px);
			background-color: #b3b3b3;
		}
	}
}

@media screen and (max-width:750px) {
	.news {
		&_box {
			padding: 12px;
		}
		&_item {
			padding-bottom: 0;
			margin-bottom: 12px;
		}
		&_txt {
			padding: 12px 0;
		}
		&_left {
			width: 100%;
			height: 240px;
			margin-bottom: 12px;
			float: none;
		}
		&_shop {
			padding-bottom: 12px;
			margin-bottom: 12px;
		}
	}
}