.form {
	&_title {
		margin-bottom: 30px;
	}

	&_box>div {
		width: 50%;
		padding: 20px;
	}

	&_group {
		margin-bottom: 18px;

		input,
		textarea,
		label {
			@include size(100%, 100%);
			@include lineht(48px);
			text-indent: 12px;
			border: $common-border;
			background-color: transparent;
			border-radius: 10px;
			@include placehold($nine-color, 16px);
		}

		textarea {
			height: 182px;
			resize: none;
		}
	}

	&_btn {
		button {
			display: block;
			margin: 0 auto 30px;
			@include size(300px, 48px);
			text-align: center;
			line-height: 48px;
			@include colbg($white-color, $main-color);
			font-size: 16px;
			border-radius: 10px;
		}
	}

	&_wrap {
		padding: 0 20px 20px;
	}
	&_file {
		text-align: center;
		line-height: 30px;
	}
}

.contact {
	&_box {
		padding-top: 50px;
	}

	&_list {
		padding: 30px;
	}
	&_wrap {
		padding: 100px 100px;
		background: url(../img/contact_bg.png) no-repeat center;
	}

	&_item {
		float: left;
		border-right: $common-border;
		width: $ful-width/3;

		&:last-child {
			border-right: none;
		}
	}

	&_infor {
		display: block;
		width: 120px;
		margin: 0 auto;
	}
}

@media screen and(max-width:750px) {
	.contact {
		&_box {
			padding-top: 12px;
		}

		&_list {
			padding: 12px 0;
		}

		&_font {
			font-size: 14px;
		}
		&_map {
			padding: 12px;
		}
		&_wrap {
			padding: 12px 0;
		}
		&_desc {
			padding: 0 12px;
		}
	}
	
	.form {
		&_title {
			margin-bottom: 12px;
		}

		&_box>div {
			width: 100%;
			padding: 0 10px;
		}

		&_group {
			margin-bottom: 10px;
		}

		&_btn {
			button {
				margin-bottom: 12px;
			}
		}

		&_wrap {
			padding: 0 10px;
		}
	}
}