.common {
	&_box {
		@extend .clearfix;
		margin-top: 40px;
	}
	&_pad {
		padding: 30px 0;
	}

	&_title {
		width: 1200px;
		display: inline-block;
		text-align: center;
		font-size: 42px;		
		color: $main-color;

		h6 {
			position: relative;
			top: 5px;
			font-weight: bold;
			text-transform: uppercase;
			letter-spacing: 1px;
			opacity: .15;
		}

		p {
			position: relative;
			line-height: 36px;

			&:before,
			&:after {
				content: '';
				position: absolute;
				top: 22px;
				@include size(200px, 1px);
				background-color: $border-color;
			}

			&:before {
				left: 240px;
			}

			&:after {
				right: 240px;
			}
		}

		&.left {
			text-align: left !important;

			h6 {
				&:after {
					left: 6px;
					transform: translateX(0);
				}
			}
		}
	}

	&_title2 {
		display: inline-block;
		font-size: 36px;
		font-weight: bold;
		color: $main-color;

		span {
			text-transform: uppercase;
			letter-spacing: 1px;
			opacity: .2;
			font-family: 'arial';
		}
	}

	&_btn {
		display: inline-block;
		@include size(120px, 40px);
		line-height: 38px;
		text-align: center;
		font-size: 16px;
		border: 1px solid $main-color;
		background-color: transparent;
		color: $main-color;
		transition: $easein;

		&.slot,
		&:hover {
			@include colbg($white-color, $main-color);
		}
		&.round {
			border-radius: 40px;
		}
	}

	&_btn.white {
		color: $white-color;
		border-color: $white-color;
		background-color: transparent;
	}

	&_head {
		display: inline-block;
		font-size: 18px;
		font-weight: bold;

		small {
			font-size: 14px;
			font-weight: normal;
			text-transform: uppercase;
			font-family: 'Arial';
			color: $nine-color;
		}

		&.white {
			color: $white-color;

			small {
				color: $white-color;
			}
		}
	}

	&_left {
		width: 210px;
		float: left;
	}

	&_menu {
		border-top: 20px solid $main-color;
	}

	&_list {
		li {
			@include lineht(60px);
			text-align: center;
			font-size: 18px;
			border-bottom: $common-border;

			a {
				display: block;
			}

			&.active,
			&:hover {
				color: $main-color;
			}
		}
	}

	&_right {
		float: right;
		width: 960px;
		min-height: 560px;
	}

	&_crumb {
		margin-top: 47px;
		padding-bottom: 10px;
		border-bottom: $common-border;
	}

	&_submenu {
		font-size: 14px;
		color: $six-color;
	}

	&_detail {
		overflow: hidden;

		h1,
		h2,
		h3,
		h4,
		h5,
		h6 {
			font-weight: bold;
			margin-top: 20px;
			margin-bottom: 20px;
			text-align: center;
		}

		h1 {
			font-size: 28px;
		}

		h2 {
			font-size: 24px;
		}

		h3 {
			font-size: 22px;
		}

		h4 {
			font-size: 20px;
		}

		h5 {
			font-size: 18px;
		}

		h6 {
			font-size: 16px;
		}

		p {
			text-align: justify;
			text-indent: 2em;
			margin-bottom: 20px;
			line-height: 1.8;

			img {
				max-width: 100% !important;
				height: auto;
			}
		}
	}

	&_nav {
		&_list {
			display: inline-block;
			@extend .clearfix;
		}

		$border-nav: 2px solid $main-color;

		&_item {
			float: left;
			width: 230px;
			font-size: 16px;
			text-align: center;
			height: 40px;
			line-height: 36px;
			border-top: $border-nav;
			border-bottom: $border-nav;
			border-left: $border-nav;

			&:last-child {
				border-right: $border-nav;
			}

			&.active {
				@include colbg($white-color, $main-color);
			}
		}
	}

	&_pages {
		padding: 30px 0;
		font-size: 16px;
		border-top: $common-border;

		a {
			@extend .text_ellipsis;
			display: block;
			line-height: 30px;
		}
	}
	&_p12 {
		padding: 30px 0;
	}
}

@media screen and(max-width:750px) {
	.common {
		&_title {
			width: 100%;
			font-size: 20px;

			h6 {
				top: 12px;
			}

			p {

				&:before,
				&:after {
					top: 19px;
					@include size(100px, 1px);
				}

				&:before {
					left: 5%;
				}

				&:after {
					right: 5%;
				}
			}
		}
		&_pad {
			padding: 12px 0;
		}
		&_p12 {
			padding: 12px;
		}

		&_title2 {
			font-size: 18px;
		}

		&_box {
			margin-top: 12px;
		}

		&_head {
			font-size: 12px;

			small {
				font-size: 12px;
			}
		}

		&_submenu {
			font-size: 12px;
		}

		&_crumb {
			margin-top: 12px;
			font-size: 12px;
		}

		&_container {
			padding: 0 10px;
		}

		&_detail {
			padding-top: 12px;
		}

		&_left,
		&_right {
			width: 100%;
		}

		&_right {
			min-height: auto;
		}

		&_menu {
			border-width: 8px;
		}

		&_list {
			overflow: hidden;

			li {
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
				float: left;
				width: 25%;
				height: 40px;
				line-height: 40px;
				font-size: 14px;
				border-left: $common-border;

				&:nth-child(4n+4),
				&:last-child {
					border-right: $common-border;
				}
			}
		}

		&_nav {
			&_list {
				display: flex;
				justify-content: space-around;
				padding: 0 10px;
			}

			$border-nav: 1px solid $main-color;

			&_item {
				float: none;
				width: auto;
				flex: 1;
				overflow: hidden;
				font-size: 14px;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
		}
	}
}