.withdraw {
	&_box {

	}
	&_ipt {
		display: inline-block;
		width: 120px;
	}
	&_btn {
		cursor: pointer;
	}
	&_link {
		display: inline-block;
		@include size(312px,40px);
		line-height: 40px;
		border-radius: 6px;
		@include colbg($white-color,$main-color);
		&:hover {
			color: $white-color;
		}
	}
	&_list {
		padding: 30px;
	}
}

@media screen and(max-width:750px) {
	.withdraw {
		&_list {
			padding: 12px;
		}
	}
}