.offer {
	&_title {
		font-size: 18px;
		margin-right: 24px;
		@include lineht(40px);
	}
	&_form {
		background-color: $bg-color;
		.form_group {
			input,
			label {
				background-color: $white-color;
			}
			textarea {
				height: 114px;
				background-color: $white-color;
			}
			.form_lb {
				text-align: center;
				color: $nine-color;
				font-size: 16px;
				cursor: pointer;
			}
		}
	}
}

@media screen and(max-width:750px) {
	.offer {
		&_box {
			padding: 20px 0;
		}

		&_title {
			font-size: 15px;
			@include lineht(30px);
		}
	}
}