.ensure {
	&_item {
		overflow: hidden;
		display: block;
		padding: 30px 20px;
		border-bottom: $common-border;
		&:nth-child(1) {
			border-top: $common-border;
		}
	}
	&_icon {
		@include size(120px,120px);
		margin-right: 20px;
	}
	&_txt {
		width: 1020px;
		overflow: hidden;
	}
	&_cont {
		@include rowhide(2,30px);
		margin: 10px 0;
		text-indent: 2em;
	}
}

@media screen and (max-width:750px) {
	.ensure {
		&_item {
			padding: 12px;
			display: flex;
			align-items: center;
		}
		&_icon {
			width: 80px;
			height: 80px;
			margin-right: 10px;
		}
		&_txt {
			width: auto;
			flex: 1;
		}
		&_title {
			font-size: 18px;
		}
	}
}