.pay {
	&_box {
		min-height: 300px;
	}
	&_type {		
		&_item {
			position: relative;
			display: block;
			@include size(108px,40px);
			line-height: 38px;
		}
		&.active {
			.pay_type_item {
				border-color: $main-color;
				color: $main-color;
				&:after {
					content: '';
					position: absolute;
					right: -1px;
					bottom: -2px;
					@include size(23px,23px);
					background: url(../img/icon_pay_type.png) no-repeat center;
				}
			}
		}
	}
}