.about {
	&_wrap {
		height: 550px;
		overflow: hidden;
		background-color: $bg-color;
		&.right {
			.about_unit.staff {
				padding-left: 620px;
			}
		}
	}
	&_box {
		padding: 30px 0;
	}

	&_pic {
		@include size(480px,360px);
		@extend .bg_cover;
		&.ful {
			@include size(50%,100%);
		}
	}

	&_unit {
		padding-right: 20px;
		&.staff {
			padding-top: 60px;
		}
		&_title {
			margin-top: 24px;
			font-size: 24px;
		}
	}

	&_cont {
		margin: 30px 0;
		width: 570px;
		font-size: 16px;
		@include rowhide(6, 32px);
	}
	&_sidebar {
		&_title {
			@include lineht(60px);
			text-align: center;
			background-color: $main-color;
			cursor: pointer;
		}
		&_cont {
			padding: 14px;
			border: $common-border;
			border-top: none;
		}
		&_icon {
			@extend .clearfix;
			li {
				float: left;
				width: $ful-width/3;
				text-align: center;
				a {
					display: block;
					i {
						font-size: 28px;
						&:hover {
							color: $main-color;
						}
					}
				}
			}
		}
		&_infor {
			margin-top: 20px;
			p {
				font-size: 14px;
				margin-bottom: 10px;
				line-height: 24px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}

@media screen and(max-width:750px) {
	.about {
		&_wrap {
			height: auto;
			&.right {
				.about_unit.staff {
					padding-left: 0;
				}
			}
		}
		&_box {
			padding: 12px 0;
		}

		&_pic {
			width: 100%;
			height: 220px;
			&.ful {
				width: 100%;
				height: 240px;
				margin-bottom: 12px;
			}			
		}

		&_unit {
			padding: 0 10px;
			&_title {
				font-size: 18px;
			}
		}

		&_cont {
			width: auto;
			height: auto;
			margin: 12px 0;
		}
		&_sidebar {
			display: none;
		}
	}
}