.order {
	&_inline {
		display: inline-block;
		margin: 0 20px;
	}
	&_box {
		overflow: hidden;
		height: 480px;
		padding: 80px 0;
		@include bg('../img/order_bg.png',cover);
	}
	&_title {
		margin-bottom: 30px;
		font-size: 52px;
		color: $main-color;
	}
	&_desc {
		font-size: 30px;
		color: #111;
	}
	&_group {
		color: $white-color;
	}
	&_btn {
		margin: 30px 20px;
		@include size(150px,40px);
		line-height: 40px;
		font-size: 18px;
		color: $white-color;
		border-radius: 6px;
	}
	&_tel {
		line-height: 54px;
		font-size: 22px;
		span {
			font-size: 36px;
			font-weight: bold;
			color: $main-color;
		}
	}
}

@media screen and(max-width:750px) {
	.order {
		&_box {
			padding: 20px 0;
			height: auto;
		}
		&_title,
		&_desc,
		&_btn,
		&_tel {
			font-size: 18px;
		}
		&_tel span {
			font-size: 20px;
		}
		&_title {
			margin-bottom: 12px;
		}
		&_btn {
			margin: 10px;
		}
		&_tel {
			line-height: 36px;
		}
	}
}