.center {
	&_item {
		padding: 60px;
		border: 1px solid $border-color;
		transition: $easein;
		cursor: default;
		&:hover {
			border-color: $main-color;
		}
	}
	&_title {
		font-size: 22px;
		margin: 20px 0;
	}
	&_line {
		background-color: $border-color;
		width: 40px;
	}
	&_cont {
		@include rowhide(4,30px);
		margin-bottom: 30px;
		font-size: 16px;
	}
}

@media screen and(max-width:750px) {
	.center {
		&_item {
			margin-bottom: 12px;
			padding: 12px 30px;
		}
		&_title {
			margin: 10px 0;
		}
		&_cont {
			margin-bottom: 12px;
		}
	}
}