.cooper {
	&_box {
		margin-top: 40px;
		background-color: $bg-color;
	}

	&_list {
		padding: 60px 0;
		@extend .clearfix;

		li {
			float: left;
			width: $ful-width/6;
		}
	}

	&_item {
		margin: 0 auto;
		@include size(170px, 50px);
		@extend .bg_contain;
	}
}

@media screen and(max-width:750px) {
	.cooper {
		&_box {
			margin-top: 12px;
		}

		&_list {
			padding: 20px 0 0;

			li {
				width: $ful-width/3;
				margin-bottom: 20px;
			}
		}

		&_item {
			@include size(102px, 30px);
		}
	}
}