.login {
	&_wrap {
		overflow: hidden;
		height: 526px;
	}
	&_unit {
		position: relative;
		height: 100%;
	}
	&_form {
		display: block;
		min-width: 320px;
		max-width: 340px;
		position: absolute;
		right: 0;
		top: 50%;
		padding: 30px 28px;
		transform: translateY(-50%);
		background-color: $white-color;
		border-radius: 4px;
		box-shadow:0px 0px 8px 0px rgba(26,25,25,0.14);
	}
	&_title {
		margin-bottom: 16px;
		font-size: 18px;
		color: #000;
	}
	&_cell {
		.login_group {
			width: 48%;
		}
	}
	&_group {
		position: relative;
		overflow: hidden;
		margin-bottom: 16px;
		border-radius: 4px;
		border: 1px solid $border-color;
	}
	&_ipt {
		width: 100%;
		padding: 0 12px;
		@include lineht(40px);
		&.btn {
			text-align: center;
			color: $white-color;
			background-color: $main-color!important;
			cursor: pointer;
		}
	}
	&_icon {
		@include position(0,0);
		@include size(40px,40px);
		line-height: 38px;
		text-align: center;
		background-color: $bg-color;
		img {
			vertical-align: middle;
		}
	}
	&_choice {
		line-height: 30px;
		margin-bottom: 16px;
	}
	&_nine {
		color: $nine-color;
	}
	&_col {
		margin-left: 12px;
		color: $main-color;
	}
	&_btn {
		@include size(100%,40px);
		line-height: 40px;
		text-align: center;
		color: $white-color;
		border-radius: 4px;
	}
	&_logo {
		width: 199px;
		height: 56px;
	}
	&_back {
		margin-top: 40px;
		font-size: 16px;
		color: $main-color;
	}
}

@media screen and(max-width:750px) {
	.login {
		&_top:after,
		&_top:before {
			display: none;
		}
		&_ding {
			height: auto;
		}
		&_top {
			display: flex;
			padding: 0 12px;
			height: 50px;
			align-items: center;
			justify-content: space-between;
		}
		&_pic,
		&_back {
			margin: 0;
			float: none;
		}
		&_logo {
			width: 107px;
			height: 30px;
		}
		&_wrap {
			padding: 12px;
			width: 100%;
			height: auto;
		}
		&_form {
			position: inherit;
			margin: 0 auto;
			padding: 20px;
			transform: none;
		}
	}
}