.case {
	&_desc {
		line-height: 26px;
		text-indent: 2em;
		text-align: justify;
	}
	&_item {
		&:nth-child(odd) {
			background-color: $bg-color;
		}
		&:nth-child(even) {
			background-color: $white-color;
		}
	}
	&_title {
		margin-bottom: 8px;
		font-size: 24px;
		color: $main-color;
	}
	&_unit {
		padding: 20px 8px 0;
	}
	&_word {
		color: #8c8c8c;
	}
	&_single {
		overflow: hidden;
		display: block;
		float: left;
		width: 296px;
		border: $common-border;
		background-color: $white-color;
		&:nth-last-child(1) {
			border-left: none;
		}
	}
	&_pic {
		width: 294px;
		height: 130px;
		display: block;
		margin: 0 auto;
	}
	&_topic {
		width: 90%;
		margin: 0 auto;
		border-top: $common-border;
		@include lineht(40px);
		transition: $easein;
		&:hover {
			color: $main-color;
		}
	}
}

@media screen and(max-width:750px){
	.case {
		&_item {
			padding: 12px;
		}
	}
}