.honor {
	&_wrap {
		padding: 30px 0;
		background-color: $bg-color;
	}
	&_unit {
		padding: 20px 0;
	}
	&_list {
		@extend .clearfix;
	}
	&_item {
		float: left;
		position: relative;
		width: $ful-width/3;
		margin-bottom: 20px;
		a {
			display: block;			
		}
		&_title {
			@extend .text_ellipsis;
			font-size: 16px;
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			@include lineht(40px);
			text-align: center;
			@include colbg($white-color,rgba(#000,.4));
		}
		&.big {
			width: $ful-width/2;
		}
	}
	&_cell {
		position: relative;
		margin: 0 auto;
		@include size(344px,240px);
		@extend .bg_cover;
		transition: $easein;
		&:hover {
			box-shadow: 0 2px 10px rgba($color: #000000, $alpha: .2);
		}
		&_title {
			@extend .text_ellipsis;
			@include lineht(30px);
			font-size: 20px;
			margin-bottom: 20px;
			color: $four-color;
			text-align: center;
			transition: $easein;
			&.big {
				font-size: 20px;
				margin-bottom: 0;
			}
			&:hover {
				color: $main-color;
			}
		}
		&.big {
			@include size(534px,372px);
			margin-bottom: 20px;
		}
	}
	&_title {
		@extend .text_ellipsis;
		text-align: center;
		font-size: 14px;
		margin-top: 12px;		
	}
}

@media screen and(max-width:750px){
	.honor {
		&_unit {
			padding: 12px 0;
		}
		&_item {
			width: $ful-width;
			margin-bottom: 20px;
			overflow: hidden;			
		}
		&_cell_title {
			font-size: 18px;
			margin-bottom: 20px;
		}
		&_item.big {
			width: $ful-width;
		}
		&_cell.big {
			width: auto;
			height: 260px;
		}
		&_cell_title.big {
			font-size: 18px;
		}
	}
}