.online {
	padding-top: 90px;
	background: url(../img/job-bg.jpg) no-repeat center;
	background-size: cover
  }
  
  .online-box {
	overflow: hidden;
	padding: 40px 30px 0;
	background-color: #fff
  }
  
  .online-list {
	margin-top: 6px
  }
  
  .online-item {
	margin-bottom: 20px
  }
  
  .online-left {
	padding-left: 10px;
	width: 442px
  }
  
  .online-title {
	position: relative;
	line-height: 36px;
	font-size: 16px
  }
  
  .online-title:before {
	content: '';
	position: absolute;
	left: -12px;
	top: 16px;
	width: 5px;
	height: 5px;
	background-color: $main-color;
	border-radius: 50%
  }
  
  .online-kind {
	line-height: 24px;
	color: #666
  }
  
  .online-kind span {
	margin-right: 16px
  }
  
  .online-right {
	display: block;
	margin-top: 20px;
	margin-left: 40px;
	width: 32px;
	height: 32px;
	background-image: url(../img/icon-job.png);
	background-position: 0 0;
	background-repeat: no-repeat;
	transition: all .2s ease-in-out;
  }
  
  .online-right:hover {
	background-position: 0 -32px
  }
  
  @media screen and (max-width:750px) {
	.online {
	  padding: 12px 10px;
	}
	.online-box {
	  padding: 20px 20px 0
	}
	.online-left {
	  width: 100%
	}
  }
  