$border-six:4px solid transparent;

.nav {
	&-top {
		line-height: $thirty-width*2;
		border-bottom: 2px solid $border-color;
	}

	&-icon {
		@include size($thirty-width*2, $thirty-width*2);
		background: url(../img/icon-home.png) no-repeat center;
		background-color: $border-color;
	}

	&-bread {
		padding-left: $twenty-width;
		overflow: hidden;
		padding-right: $twenty-width;
		font-size: 16px;
		span {
			position: relative;
			&:after {
				content: '';
				position: absolute;
				right: -20px;
				top: 7px;
				border-left: 4px solid $nine-color;
				border-bottom: $border-six;
				border-top: $border-six;
				border-right: $border-six;
			}
		}

		span:nth-of-type(1) {			
			margin-right: $twenty-width;			
		}
		span:nth-of-type(2) {			
			margin-right: $twenty-width;			
		}
		span:last-child {			
			margin-right: 0;			
		}
	}

	&-col {
		position: relative;
		color: $main-color;
		cursor: pointer;
		&:after {
			display: none;
		}		
	}

	&-item {
		position: relative;
		float: left;
		padding: 0 20px;
		font-size: 16px;
		
		a {
			display: block;
			height: 100%;
		}

		&.active,
		&:hover {
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 50%;
				transform: translateX(-50%);
				@include size(72px, 2px);
				background: -webkit-linear-gradient(left, transparent, #f5a201, transparent);
				background: -o-linear-gradient(left, transparent, #f5a201, transparent);
				background: -moz-linear-gradient(left, transparent, #f5a201, transparent);
				background: linear-gradient(to right, transparent, #f5a201, transparent);
			}
		}
	}
}

@media screen and(max-width:750px){
    .nav {
        &-list {
            float: none;
            width: $ful-width;
        }
        &-float {
            float: none;
            width: $ful-width;
            border-bottom: 1px solid $border-color;
        }
    }
}
