.header {
	&_ding {
		@include lineht(30px);
		background: url(../img/ding_bg.png) right center no-repeat;
		background-size: 50% 100%;
		border-bottom: 1px solid lighten($main-color,40%);
		&_box {
			margin-right: 60px;
		}
		&_icon {
			margin-top: 4px;
			margin-right: 12px;
		}
		&_right {
			color: $white-color;
		}
	}
	&_bd {
		@include lineht(30px);
		border-bottom: 1px solid lighten($main-color,20%);
	}
	&_link {
		display: inline-block;
		margin-right: 12px;
		margin-left: 8px;
		color: $main-color;
	}
	&_phone {
		display: none;
	}
	&_top {
		height: 100px;
		box-shadow: 0 1px 0 $border-color;
	}
	&_logo {
		font-size: 0;
		margin-top: 20px;
	}
	&_right {
		margin-top: 28px;
	}
	&_nav {
		&_show {
			display: none;
			opacity: 0;
			position: absolute;
			top: 48px;
			left: 50%;
			transform: translateX(-50%);
			border: $common-border;
			z-index: 8;
			a {
				display: block;
				height: 36px;
				white-space: nowrap;
				line-height: 36px;
				padding: 0 12px;
				font-size: 14px;
				margin-bottom: 1px;
				background-color: $white-color;
				color: $six-color;
				&:hover {
					@include colbg($white-color,$main-color);
				}
				&:nth-last-child(1) {
					margin-bottom: 0;
				}
			}
		}
	}
	&_item {
		position: relative;
		float: left;
		text-align: center;
		margin-left: 50px;
		line-height: 24px;
		text-transform: uppercase;
		transition: $easein;
		a {
			display: block;
		}
		&.active,
		&:hover {
			color: $main-color;
		}
		&:hover {
			.header_nav_show {
				display: block;
				opacity: 1;
			}
		}
	}
	&_hd {
		height: 50px;
		padding: 0 10px;
		overflow: hidden;
		h1 {
			margin-top: 10px;
			font-size: 0;
		}
	}
	&_icon {
		i {
			font-size: 24px;
			margin-left: 14px;
		}
	}
	&_search {
		display: none;
		position: relative;
		padding: 20px 10px;
		background-color: $bg-color;
		border-bottom: 2px solid $main-color;
		i {
			@include position(18px,24px);
			display: block;
			font-size: 15px;
			color: $nine-color;
		}
	}
	&_text {
		height: 30px;
		.txt {
			flex: 1;
			height: 30px;
			line-height: 28px;
			overflow: hidden;
			text-indent: 30px;
			border: $common-border;
			border-right: none;
			background-color: $white-color;
			@include placehold($nine-color,13px);
		}
		.search {
			@include size(60px,30px);
			@include colbg($white-color,$main-color);
		}
	}
}

@media screen and(max-width:750px) {
	.header {
		&_pc {
			display: none;
		}

		&_phone {
			display: block;
		}
		&_search {
			display: block;
		}
	}
}