.look_more {
  display: block;
  width: 114px;
  height: 30px;
  line-height: 30px;
  margin: 5px auto;
  font-size: 16px;
  color: $white-color;
  background: $look-btn;

  &:hover {
    color: $white-color;
    background: $look-btn;
  }
}

.look_info {
  font-size: 14px;
  color: $tre-color;
}

.scan_subtitle {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
  color: $main-color;
}

.scan_price_info {
  margin-top: 10px;
  color: $tre-color;
}

.scan_yprice {
  text-decoration: line-through;
  color: $yprice-color;
}

.scan_xprice {
  color: $xprice-color;
}

.scan_price_bg {
  width: 100%;
  height: 36px;
  background: url("../img/scan_arrow.png") no-repeat center center;
}

.cont_title {
  font-size: 28px;
  color: $four-color;
}

.voice_syn_box {
  box-shadow: 0px 8px 15px 2px rgba(4, 0, 0, 0.06);
}

// 
.voice_left_title,
.voice_right_title {
  font-size: 24px;
  text-align: center;
}

.voice_left_title {
  color: #028efb;
}

.voice_right_title {
  color: #40cba2;
}

.voice_left_bg {
  text-align: center;
  color: #e5e5e5;
}

.voice_left_sub {
  color: $yprice-color;
}

.voice_table_item {
  height: 40px;
}

.success_left,
.success_cell {
  position: relative;
}

.success_cell_title {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  color: #fff;
  background: rgba(0, 0, 0, 0.4);
}

// 
.voice_box {
  padding: 40px 0;
  background: rgba(245, 247, 250, 1);
}

.voice_adv_item-icon {
  width: 110px;
  height: 110px;
  margin: 0 auto;
}

.voice_adv_subtitle {
  color: #9a9fb0;
}

.about_title {
  font-size: 24px;
  font-weight: bold;
  color: $four-color;
}

.about_txt {
  width: 670px;
  font-size: 16px;
  line-height: 25px;
  color: $gray-color;
}

.secret_title {
  margin-top: 10px;
  text-indent: 0;
}

.scree.lab_reset {
  right: 142px !important;
}

.lab_view {
  position: absolute;
  right: 12px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.lab_wrap.active {
  height: 0;
  overflow: hidden;
}

.search_input {
  width: 75%;
}

.case_more {
  position: absolute;
  right: 10px;
  top: 50%;
}

.am-accordion-title {
  display: flex;
  align-items: center;
  height: 95px;
  font-size: 16px;
  color: $four-color;
  background: #eaf8ff;
}

.am-accordion-default .am-accordion-title:hover {
  color: $four-color;
}

.am-accordion-default .am-active .am-accordion-title {
  background: #1673e6;
  color: #fff;

  .spe_subtitle {
    color: #fff;
  }

  .lmx_accordion_icon {
    a {
      display: block;
      width: 38px;
      height: 38px;
      margin: 0 5px;

      &:nth-child(1) {
        background: url("../img/start_after.png") no-repeat center center;
        background-size: 100% 100%;
      }

      &:nth-child(1).active {
        background: url("../img/stop_after.png") no-repeat center center;
        background-size: 100% 100%;
      }

      &:nth-child(2) {
        background: url("../img/video_after.png") no-repeat center center;
        background-size: 100% 100%;
      }

      &:nth-child(3) {
        background: url("../img/download_after.png") no-repeat center center;
        background-size: 100% 100%;
      }
    }
  }
}

.voice_table_icon {
  display: flex;
  height: 42px;
  justify-content: space-around;

  a {
    width: 24px;
    height: 24px;

    &:nth-child(1) {
      background: url("../img/start_before.png") no-repeat center center;
      background-size: 100% 100%;
    }

    &:nth-child(1).active {
      background: url("../img/stop_before.png") no-repeat center center;
      background-size: 100% 100%;
    }

    &:nth-child(2) {
      background: url("../img/video_before.png") no-repeat center center;
      background-size: 100% 100%;
    }
  }
}

.spe_subtitle {
  font-size: 14px;
  color: $yprice-color;
}

// modal
.am-modal-dialog {
  background: transparent;
}

.am-close {
  top: -8px !important;
  right: -4px !important;
  color: #fff;
  opacity: 1;
}

.am-close:focus,
.am-close:hover {
  color: #fff !important;
  opacity: 1;
}

#video_control {
  outline: none;
}

// 
.lmx_accordion_icon {
  display: flex;

  a {
    display: block;
    width: 38px;
    height: 38px;
    margin: 0 5px;

    &:nth-child(1) {
      background: url("../img/start_before.png") no-repeat center center;
      background-size: 100% 100%;
    }

    &:nth-child(1).active {
      background: url("../img/stop_before.png") no-repeat center center;
      background-size: 100% 100%;
    }

    &:nth-child(2) {
      background: url("../img/video_before.png") no-repeat center center;
      background-size: 100% 100%;
    }

    &:nth-child(3) {
      background: url("../img/download_before.png") no-repeat center center;
      background-size: 100% 100%;
    }
  }
}

.am-accordion-default {
  border-radius: none;
  box-shadow: none;
}

.am-accordion-content {
  border: 1px solid #ccc;
}

.lmx_tabs_nav {
  float: left;
  overflow-x: auto;
}

[data-am-widget="tabs"] .am-tabs-nav {
  width: 180px;
  flex-direction: column;
}

[data-am-widget="tabs"] .am-tabs-nav li {
  border-bottom: 1px solid $ccc-color;
}

.am-tabs-bd {
  border: none;
}

.am-accordion-default.am-margin-top-xl {
  margin-top: 100px;
}

.am-tabs-default .am-tabs-nav {
  background: #fff;
}

.am-tabs-default .am-tabs-nav a {
  padding-left: 10px;
  padding-right: 10px;
}

.am-tabs-default .am-tabs-nav > .am-active a {
  color: $four-color;
  background: #f2f2f2;
}

.am-table.lmx_table {
  width: 50%;
}

.am-table > tbody > tr > td {
  border-top: none;
  border-bottom: 1px solid $ccc-color;
}

// div
.lmx_accordion {
  display: inline-block;
}

.lmx_accordion_title {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.lmx_accordion_btn {
  display: block;
  width: 100px;
  height: 40px;
  margin-left: 20px;
  line-height: 40px;
  text-align: center;
  background: #fff;
  color: $four-color;
}

.lmx_btn_outline {
  span {
    display: block;
    width: 77px;
    height: 25px;
    line-height: 24px;
    text-align: center;
    border: 1px solid #8c8c8c;
    color: #8c8c8c;
  }
}

// 
.last_view {
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  text-align: center;
  color: $main-color;
  background: #f2f2f2;
}

// 
.music_bg {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  display: none;
  width: 100%;
  height: 150px;
  background: rgba(0, 0, 0, 0.6);
}

.music_title {
  padding: 20px;
  color: #fff;
}

.am-popup {
  height: 300px;
}

.am-popup-hd .am-close {
  right: 10px !important;
  top: 8px !important;
}

.am-popup-hd .am-close:hover {
  color: #555 !important;
}

.ask_him_btn {
  cursor: pointer;
}

.contactTencent {
  display: block;
  width: 210px;
  height: 50px;
  margin: 0 auto;
  background: url("../img/qqc.png") no-repeat center center;
  background-size: 100%;
}

@media screen and (max-width: 750px) {
  .am-nav > li {
    width: auto;
  }

  .am-table.lmx_table {
    width: 100%;
  }

  .lmx_tabs_nav {
    float: none;
  }

  [data-am-widget="tabs"] .am-tabs-nav {
    width: auto;
    flex-direction: row;
  }
}

