}
[data-am-widget="tabs"] .am-tabs-nav.lmx_modal li {
  border: none;
}
.am-tabs-default .am-tabs-nav.lmx_modal > .am-active a {
  width: 100px;
  border-radius: 50px;
  background: #1673e6;
  color: #fff;
}
.am-tabs-default .am-tabs-nav.lmx_modal a {
  width: 100px;
}
.am-popup.lmx_modal {
  width: 60%;
  height: auto;
  margin: 0;
  left: calc(50% - 30%);
  top: 25%;
}
@media screen and (max-width: 750px){
  .am-popup.lmx_modal {
    width: 80%;
    height: auto;
    margin: 0;
    left: calc(50% - 40%);
    top: 25%;
  }
}
@media (min-height: 630px) and (min-width: 630px) {
  .am-popup.lmx_modal {
    width: 60%;
    height: auto;
    margin: 0;
    left: calc(50% - 30%);
    top: 25%;
  }
}
