.project {
	&_box {
		padding-top: 30px;
	}
	&_item {
		display: block;
		@extend .clearfix;
		margin-bottom: 30px;
		background-color: $bg-color;
		&>div {
			width: $ful-width/2;
		}
		&:nth-child(even) {
			.project_pic {
				float: left;
			}
			.project_txt {
				float: right;
			}
		}
		&:nth-child(odd) {
			.project_pic {
				float: right;
			}
			.project_txt {
				float: left;
			}
		}
	}
	&_pic {
		@include size(100%,260px);
		@extend .bg_cover;
	}
	&_txt {
		padding: 30px;
	}
	&_title {
		font-size: 18px;
		color: $main-color;
		margin-bottom: 24px;
	}
	&_cont {
		@include rowhide(5,28px);
	}
}

@media screen and(max-width:750px){
	.project {
		&_item>div {
			width: $ful-width;
		}
		&_box {
			padding-top: 12px;
		}
		&_txt {
			padding: 12px;
		}
		&_pic {
			height: 210px;
		}
		&_item,
		&_title {
			margin-bottom: 10px;
		}
		&_cont {
			height: auto;
		}
	}
}