.events {
	&_box {
		padding-top: 30px;
	}
	&_unit {
		padding-bottom: 30px;
	}
	&_title {
		font-size: 18px;
		font-weight: bold;
		color: $main-color;
		small {
			margin-left: 12px;
			font-size: 14px;
			color: $nine-color;
		}
	}
	&_list {
		margin-top: 20px;
		padding-left: 20px;
	}
	&_item {
		position: relative;
		padding-left: 20px;		
		@include lineht(30px);
		font-size: 14px;
		a {
			display: block;
			@extend .text_ellipsis;
		}
		&:before {
			content: '';
			display: block;
			@include position(0,11px);
			@include size(8px,8px);
			border-radius: 50%;
			background-color: $main-color;
		}
		&:hover {
			color: $main-color;
		}
	}
}