.report {
	&_wrap {
		padding: 40px 0;
	}
	&_group {
		margin-bottom: 20px;
		input {
			display: block;
			margin: 0 auto;
			@include size(690px,50px);
			line-height: 48px;
			font-size: 20px;
			text-indent: 12px;
			border: 1px solid $main-color;
			@include placehold($nine-color,20px);
		}
	}
	&_tips {
		color: red;
	}
}

@media screen and (max-width:750px){
	.report {
		&_group {
			input {
				width: 300px;
				height: 32px;
				line-height: 30px;
				font-size: 15px;
				@include placehold($nine-color,15px);
			}
		}
	}
}