body {
    font-size: 14px;
    color: $gray-color;
    font-family: "Segoe UI","Lucida Grande",Helvetica,Arial,"Microsoft YaHei",FreeSans,Arimo,"Droid Sans","wenquanyi micro hei","Hiragino Sans GB","Hiragino Sans GB W3",FontAwesome,sans-serif;
    line-height: 1.432;
    background-color: #fff;
    -webkit-touch-callout: none;
    -webkit-font-smoothing: antialiased;
}

.float_lt {
    float: left;
}

.float_rt {
    float: right;
}

.clearfix:after {
    content: '';
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
}

.clearfix {
    zoom: 1;
}

.text_ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.align_center {
    text-align: center;
}

.flex {
    display: flex;
}

.flex_center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.flex_around {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.flex_between {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.flex_normal {
    display: flex;
    align-items: center;
}

.flex_1 {
    flex: 1;
    overflow: hidden;
}

.bg_cover {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.bg_contain {
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.common_wrap {
	max-width: $common-width;
	margin: 0 auto;
}

.word {
    text-transform: uppercase;
}

.xie {
    font-style: italic;
}

.mar_auto {
	margin-left: auto;
	margin-right: auto;
}

textarea {
    width: 100%;
    height: 100%;
    resize: none;
}

.pc {
    display: block;
}

.mobile {
    display: none;
}

@media screen and(max-width:750px) {
    .pc {
        display: none;
    }
    
    .mobile {
        display: block;
    }
}
