.scan {
	&_box {
		color: $gray-color;
	}
	&_cont {
		width: 80%;
		margin: 24px auto 14px;
		@include rowhide(2,30px);
	}
	&_block {
		@include unit(34px,4px);
	}
	&_wrap {
		position: relative;
		&:after {
			content: '';
			display: block;
			@include position(50%,50%);
			transform: translate(-50%,-50%);
			@include size(1px,90px);
			background-color: $border-color;
		}
	}
	&_item {
		&:nth-child(odd) {
			.scan_cell {
				margin-left: 164px;
			}
		}
		&:nth-child(even) {
			.scan_cell {
				margin-left: 16px;
			}
		}
	}
	&_kind {
		position: relative;
	}
	&_cell {
		width: 386px;
		@include rowhide(3,30px);
		text-align: justify;
	}
	&_pic {
		@include size(194px,194px);
		border-radius: 50%;
		display: block;
		margin: 8px auto;
	}
	&_kuai {
		@include unit;
	}
	&_bg {
		@include position(0,120px);
		z-index: -2;
		@include size(100%,120px);
		overflow: hidden;
		@include bg('../img/scan_bg.png',cover);
	}
}

@media screen and(max-width:750px) {
	.scan {
		&_cont {
			height: auto;
			margin: 12px auto;
			width: 90%;
			-webkit-line-clamp: inherit;
		}
		&_wrap:after {
			display: none;
		}
		&_cell {
			width: auto;
			height: auto;
			-webkit-line-clamp: inherit;
			margin: 0!important;
		}
		&_pic {
			width: 160px;
			height: 160px;
		}
		&_bg {
			top: 200px;
			background-size: contain;
		}
	}
}