dt+dd {
	margin: 0;
}
.footer {
	&_box {
		color: $gray-color;
		a {
			&:hover {
				color: $main-color;
			}
		}
	}

	&_link {
		overflow: hidden;
		margin-bottom: 12px;

		&_title {
			position: relative;
			font-size: 16px;
			margin-bottom: 12px;
			&:after {
				content: '';
				position: absolute;
				left: 80px;
				top: 50%;
				width: 100%;
				height: 1px;
				background-color: $border-color;
			}
		}

		&_list {

			li {
				float: left;
				margin-right: 20px;
				margin-bottom: 12px;
			}
		}
	}

	&_dh {
		margin-bottom: 24px;

		a {
			float: left;
			width: $ful-width/8;
			display: block;
			text-align: center;
			border-left: 2px solid $white-color;

			&:last-child {
				border-right: 2px solid $white-color;
			}
		}
	}

	&_btm {
		overflow: hidden;
		@include lineht(50px);

		&_right {
			a {
				&:first-child {
					margin-right: 10px;
				}
			}
		}
	}

	&_top {
		padding-top: 30px;
	}

	&_wrap {
		width: $ful-width;
		padding-top: 12px;
	}

	&_nav {
		float: left;
		width: $ful-width/3;

		dl {
			overflow: hidden;
		}
		

		dt {
			font-size: 16px;
			margin-bottom: 10px;
		}

		dd {
			// margin-bottom: 16px;
			margin-right: 16px;
			// float: left;
			cursor: pointer;

			&:hover {
				color: $main-color;
			}
		}
	}
	&_contact {
		dl {
			width: 100%;
			dd {
				width: 100%;
			}
		}
	}
	&_serve {
		dd {
			@extend .text_ellipsis;
		}
	}

	&_ewm {
		overflow: hidden;
		@include size(150px, 150px);
		&_box {
			width: 150px;
			// float: left;
		}
		&_txt {
			margin-left: 12px;
			@include size(200px,150px);
			border-top: 1px solid $border-color;
			border-bottom: 1px solid $border-color;
		}
		&_for {
			@include rowhide(3,26px);
			font-size: 14px;
			margin-top: 30px;
			text-align: justify;
		}
	}
	&_nav_wrap {
		display: none;
	}
	&_erweima {
		margin-top: 20px;
	}
}
.min-width-50{
	min-width: 50%;
}
@media screen and(max-width:750px) {
	.footer {
		&_top {
			padding: 16px 10px 12px;
		}

		&_link {
			&_title {
				width: 100%;
				margin-bottom: 12px;
			}

			&_list {
				width: 100%;

				li {
					margin-right: 20px;
					margin-bottom: 6px;
					font-size: 14px;
				}
			}
		}

		&_dh {
			margin-bottom: 12px;
			font-size: 12px;

			a {
				width: $ful-width/4;
				margin-bottom: 8px;
				border-width: 1px!important;

				&:nth-child(4n+4) {
					border-right: 2px solid $white-color;
				}
			}
		}

		&_btm {
			padding: 6px 12px;
			line-height: 25px;
			text-align: center;
			height: auto;

			&_right,
			&_left {
				white-space: nowrap;
				overflow-x: scroll;
				width: 100%;
			}
			&_left {
				white-space: normal;
			}
		}

		&_wrap {
			padding-top: 0;
		}

		&_left {
			padding-bottom: 10px;
			margin-bottom: 12px;
			border-bottom: 1px solid $six-color;
		}

		&_nav {
			width: $ful-width;
			// display: flex;

			dl {
				width: $ful-width;
				height: auto;
				border-right: none;
			}

			dt {
				width: 100%;
				margin-bottom: 12px;
				font-size: 14px;
			}

			dd {
				width: 25%;
				float: left;
				font-size: 14px;
				margin-top: 0;
				margin-right: 0;
				margin-bottom: 10px;
			}
		}

		&_contact {
			width: $ful-width;
			margin-bottom: 10px;
			border-bottom: 1px solid $white-color;

			dl {
				text-align: left;
			}

			dd {
				width: 100%;
			}
		}
		&_nav_wrap {
			display: block;
		}
		&_ewm {
			&_txt {
				width: auto;
				flex: 1;
				overflow: hidden;
			}
		}
	}
}