.serve {
	&_title {
		position: relative;
	}
	&_about {
		&_txt {
			@include size(670px,300px);
			overflow: hidden;
			.cont {
				@include rowhide(8,30px);
				font-size: 16px;
				text-align: justify;
			}
			&.language {
				width: 620px;
				padding: 75px 0;
				.cont {
					@include rowhide(5,30px);
				}
			}
		}
		&_pic {
			width: 450px;
			height: 300px;
			&.language {
				width: 510px;
			}
		}
	}
	&_cell {
		display: block;
		padding: 24px 12px;
		margin-bottom: 20px;
		border: $common-border;
		transition: $easein;
		cursor: default;
		&:hover {
			border-color: $main-color;
		}
	}
	&_icon {
		display: block;
		margin-right: 20px;
		margin-top: 40px;
	}
	&_txt {
		width: 168px;
		overflow: hidden;
		&_cont {
			@include rowhide(4,22px);
			margin: 12px 0;
			text-align: justify;
		}
	}
	&_desc {
		width: 900px;
		line-height: 30px;
		text-indent: 2em;
		text-align: justify;
	}
	&_tb {
		thead tr th {
			font-size: 18px;
			font-weight: normal;
		}
		tbody tr td {
			font-size: 16px;
		}
	}
	&_tips {
		line-height: 30px;
		&_title {
			font-size: 18px;
			color: #f00;
		}
	}
}

@media screen and (max-width:750px) {
	.about_txt {
		width: 100%!important;
		height: auto!important;
	}
	.serve {
		&_about {
			padding: 12px 12px 0;
		}
		&_about_txt,
		&_about_pic {
			width: 100%!important;
			height: auto!important;
		}
		&_about_txt .cont {
			height: auto;
		}
		&_about_txt.language {
			padding: 12px 0;
		}
		&_title {
			&.left {
				text-align: left!important;
				padding-left: 12px;
			}
			font-size: 20px;
		}
		&_cell {
			display: flex;
			align-items: center;
			padding: 12px;
			margin-bottom: 12px;
		}
		&_cell:before,
		&_cell:after {
			display: none;
		}
		&_icon {
			margin: 0;
			margin-right: 12px;
		}
		&_txt {
			width: auto;
			flex: 1;
		}
		&_txt_cont {
			height: 66px;
			-webkit-line-clamp: 3;
		}
		&_desc {
			width: auto;
			margin: 0 12px!important;
		}
		&_tb {
			padding: 12px;
		}
		&_show::-webkit-scrollbar {
			display: block;
		}
		&_tips {
			padding: 0 12px;
			text-align: justify;
		}
	}
}