@import 'variable';
@import 'reset';
@import 'mixin';
@import 'mylei';
@import 'change';
@import 'music';
@import 'range';

.am-slider-default .am-control-nav {
	bottom: 20px;
}

.am-slider-default .am-control-nav li a {
	@include size(20px, 20px);
}

.am-slider-default .am-control-nav li a.am-active {
	cursor: pointer;
	background-color: $main-color;
}

.am-slider-default {
	margin: 0;
}

@media screen and(max-width:750px) {
	.am-slider-default .am-control-nav li a {
		width: 14px;
		height: 14px;
	}
}

.am-menu-dropdown1 .am-menu-toggle {
	color: $tre-color;
	top: -49px;
}

// 
.am-gotop-fixed .am-gotop-icon {
	display: none;
	background-color: rgba($color: $main-color, $alpha: .4);
	border-radius: 50%;
}

@media screen and (max-width:750px) {
	.am-gotop-fixed .am-gotop-icon {
		display: block;
	}
}

.am-with-fixed-navbar {
	padding-bottom: 0;
}

@media screen and (max-width:750px) {
	.am-with-fixed-navbar {
		padding-bottom: 50px;
	}
}

.am-close {
	position: absolute;
    top: 4px;
	right: 4px;
	font-size: 30px;
}

// 
.am-pagination>.am-active>a,
.am-pagination>.am-active>a:focus,
.am-pagination>.am-active>a:hover,
.am-pagination>.am-active>span,
.am-pagination>.am-active>span:focus,
.am-pagination>.am-active>span:hover {
	background-color: $main-color;
	border-color: $main-color;
}

// 
.am-table {
	margin: 0;
}

input[type=text]:read-only {
	background-color: transparent;
}

input[type=checkbox],
input[type=radio] {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	margin: 0;
	vertical-align: middle;
}

input[type=checkbox]:before,
input[type=radio]:before {
	content: '';
	display: block;
	width: 16px;
	height: 16px;
	border-radius: 50%;
	@include bg('../img/icon-choice.png',contain);
}

input[type=checkbox]:checked:before,
input[type=radio]:checked:before {
	content: '';
	background-image: url(../img/icon-choice-hov.png);
}

input:disabled {
	cursor: not-allowed!important;
}

.mid {
	&_pc {
		overflow: hidden;
		height: 200px;

		a {
			display: block;
			height: 100%;
		}
	}

	&_phone {
		overflow: hidden;
		height: 100px;
		display: none;

		a {
			display: block;
			height: 100%;
		}
	}
}

.newsBg {
	padding-bottom: 40px;
	background-color: #F5F7FA;
}

.col {
	&_four {
		color: $four-color;
	}
	&_main {
		color: $main-color;
	}
	&_red {
		color: #f00;
	}
}

@media screen and (max-width:750px) {
	.mid {
		&_pc {
			display: none;
		}

		&_phone {
			display: block;
		}
	}
	.newsBg {
		padding-bottom: 12px;
	}
}

.blue {
	background-color: $main-color;
	transition: $easein;
	&:hover {
		background-color: darken($main-color,10%);
	}
}

.orange {
	background-color: #E9812D;
	transition: $easein;
	&:hover {
		background-color: darken(#E9812D,10%);
	}
}

.mar_btm {
	margin-bottom: 20px;
	&.tre {
		margin-bottom: 30px;
	}
}

.gray {
	background-color: $bg-color;
}

.mobile_wrap {
	padding: 30px 20px;
}

.hov_main {
	transition: $easein;
	&:hover {
		color: $main-color;
	}
}

.font_tf {
	font-size: 24px;
}

@media screen and(max-width:750px) {
	.mar_btm {
		margin-bottom: 12px!important;
	}
	.pic {
		width: 100%!important;
		height: auto!important;
		float: none!important;
	}
	.auto {
		float: none!important;
		width: auto!important;
	}
	.mobile_wrap {
		padding: 12px;
	}
	.font_tf {
		font-size: 18px;
	}
}

th {
	text-align: center;
}

.font_tre {
	font-size: 16px;
	line-height: 30px;
}

.font_lt {
	font-size: 16px;
	line-height: 26px;
}

.border {
	border: $common-border;
}

.border_btm {
	border-bottom: $common-border;
}

.p12 {
	padding: 12px;
}

.no_data {
	margin: 12px 0;
	color: $nine-color;
}

