.product {
	&_box {
		padding: 40px 0;
	}

	&_item {
		width: $ful-width/3;
		float: left;
		padding: 0 20px;

		a {
			display: block;
			padding: 30px 20px;
			overflow: hidden;
			transition: $easein;

			&:hover {
				box-shadow: 0 2px 10px rgba($color: #000000, $alpha: .2);

				h5 {
					color: $main-color;
				}
			}
		}

		h5 {
			margin-bottom: 40px;
			@extend .text_ellipsis;
			font-size: 18px;
			text-align: center;
		}

		p {
			@include rowhide(2, 32px);
			font-size: 16px;
		}
	}

	&_icon {
		@include size(114px, 114px);
		margin: 20px auto 30px;
		padding: 21px;
		border-radius: 50%;
		background-color: $main-color;
	}

	&_classify {
		@extend .clearfix;
		padding: 30px 0;

		li {
			width: $ful-width/4;
			float: left;
			overflow: hidden;

			a {
				display: block;
				position: relative;
				@include size(280px, 280px);
				margin: 10px auto;
				@extend .bg_cover;
			}

			&:hover {
				.product_mengban {
					opacity: 1;
				}
			}
		}
	}

	&_mengban {
		opacity: 0;
		@include position(0, 0);
		padding: 0 12px;
		@include size($ful-width, $ful-width);
		background-color: rgba($color: $main-color, $alpha: .7);
		color: $white-color;
		transition: $easein;

		h6 {
			font-size: 24px;
			margin: 40px 0;
		}

		p {
			@include rowhide(2, 32px);
		}

		span {
			display: block;
			margin: 0 auto;
		}
	}
}

@media screen and(max-width:750px) {
	.product {
		&_box {
			padding: 0;
		}

		&_item {
			padding: 0 8px;

			a {
				padding: 10px;
			}

			h5 {
				margin-bottom: 20px;
				font-size: 16px;
			}

			p {
				display: none;
			}
		}

		&_icon {
			@include size(60px, 60px);
			margin: 10px auto;
			padding: 0;
			text-align: center;
			padding: 12px;
		}

		&_classify {
			padding: 12px 0;
			li {
				width: $ful-width/2;

				a {
					@include size(180px,180px);
				}
				&:hover {
					.product_mengban {
						opacity: 0;
					}
				}
			}
		}
	}
}