.advan {
	&_unit {
		overflow: hidden;
		&.light {
			padding: 90px 0 40px;
			background-color: #ECF4FF;
		}
	}
	&_right {
		width: 500px;
	}
	&_item {
		font-size: 18px;
		@extend .text_ellipsis;
		line-height: 64px;
		padding-left: 40px;
		background: url(../img/icon_star.png) no-repeat left center;
	}
	&_left {
		width: 540px;
		&_title {
			position: relative;
			padding-left: 10px;
			font-size: 18px;
			font-weight: bold;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				@include size(2px,16px);
				background-color: $main-color;
			}
		}
		&_cont {
			margin: 12px 0;
			@include rowhide(4,30px);
		}
	}
	&_bar {
		font-size: 16px;
		line-height: 42px;
		&_half {
			width: 50%;
		}
	}
	
}

@media screen and(max-width:750px) {
	.advan {
		&_unit {
			padding: 12px!important;
		}
		&_right {
			padding-top: 12px;
		}
		&_item {
			line-height: 32px;
			padding-left: 30px;
			font-size: 16px;
			background-size: 16px 16px;
		}
		&_bar {
			line-height: 30px;
		}
	}
}