select {
	appearance: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	@include position(0, 0);
	padding-left: 12px;
	@include size(100%, 100%);
}

.select_wrap {
	position: relative;
	padding-right: 12px;
	height: 36px;
	line-height: 34px;
	display: inline-block;
	border: $common-border;
}

.select_wrap {
	img {
		position: absolute;
		right: 12px;
		top: 50%;
		margin-top: -6px;
		z-index: 2;
	}
}

.dan {
	&_cell {
		margin-bottom: 20px;
		border-bottom: $common-border;
	}
	&_flow {
		justify-content: center;
		display: flex;
		align-items: center;

		li {
			margin: 0 12px;

			img {
				vertical-align: middle;
			}
		}

		&_item {
			padding: 0 20px;
			@include lineht(40px);
		}

		&_item.active {
			border-radius: 40px;
			@include colbg($white-color, $main-color);
		}
	}

	&_form {
		&_inline {
			float: left;
			margin-right: 12px;

			img {
				vertical-align: middle;
			}
		}

		&_language {
			width: 152px;
		}
		&_target {
			width: 222px;
		}

		&_wrap {
			padding: 12px 0 20px;
		}

		&_arrow {
			height: 36px;
			line-height: 34px;
			width: 44px;
			border: $common-border;
		}
	}
	&_file {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 30px 0;
		margin-bottom: 70px;
		background-color: $border-color;
		&_txt {
			position: absolute;
			right: 12px;
			top: 50%;
			transform: translateY(-50%);
			span {
				margin: 0 4px;
				color: #f00;
			}
		}
		&_btn {
			padding: 0 20px;
			margin: 0 20px;
			@include lineht(34px);
			text-align: center;
			background-color: $white-color;
			cursor: pointer;
			img {
				vertical-align: middle;
				margin-right: 10px;
			}
		}
		&_name {
			@include position(12px,50%);
			transform: translateY(-50%);
			font-size: 15px;
		}
	}
	&_upload {
		margin-top: 20px;
		&_item,
		&_right {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&_item {
			height: 46px;
			border-top: $common-border;
		}
		&_title {
			img {
				margin-right: 12px;
			}
		}
		&_right {
			width: 350px;
		}
		&_progress {
			span {
				display: inline-block;
			}
		}
		&_status {
			position: relative;
			margin-right: 12px;
			@include size(102px,8px);
			border: $common-border;
		}
		&_rate {
			@include position(0,0);
			width: 20%;
			height: 8px;
			display: inline-block;
			background-color: $main-color;
		}
		&_dele {
			display: block;
			margin-left: 12px;
			cursor: pointer;
		}
	}
	&_level {
		overflow: hidden;
		padding: 20px 0;
		&_item {
			margin-bottom: 12px;
			&:nth-last-child(1) {
				margin-bottom: 0;
			}
			cursor: pointer;	
			&.active {
				.dan_level_cell {
					border-color: $main-color;
				}
				.dan_level_ipt {
					border-color: $main-color;
					background: url(../img/icon-choice-hov.png) no-repeat center;
					background-size: cover;
				}
				.dan_level_txt {
					color: $gray-color;
				}
			}
		}
		&_cell {
			padding: 20px;
			border: 1px dashed $border-color;
		}
		&_ipt {
			@include size(20px,20px);
			margin-right: 20px;
			border: 1px solid $gray-color;
			border-radius: 50%;
		}
		&_txt {
			width: 280px;
			color: #8c8c8c;
		}
		&_cont {
			@include rowhide(3,20px);
		}
	}
	&_sex {
		&_item {
			line-height: 46px;
			margin-right: 20px;
		}
	}
	&_date {
		margin: 20px 0;
		width: 200px;
	}
	&_area {
		margin: 20px 0;
		padding: 10px;
		max-width: 1000px;
		height: 100px;
	}
	&_input {
		margin-bottom: 20px;
		&_item {
			margin: 20px 0;
			line-height: 34px;
			height: 36px;
			&:nth-last-child(1) {
				margin: 20px 0 0;
			}
		}
		&_ipt {
			display: block;
			width: auto;
			height: 100%;
			padding: 0 12px;
		}
		&_lb {
			width: 50px;
			margin-right: 12px;
			position: relative;
			&.xing:before {
				content: '*';
				@include position(-10px,0);
				color: #f00;
			}
		}
	}
	&_success {
		padding-top: 70px;
		&_show {
			display: block;
			text-decoration: underline;
			color: #5FB878;
			&:hover {
				text-decoration: underline;
				color: #5FB878;
			}
		}
		span {
			margin: 0 12px;
			color: #8c8c8c;
		}
	}
}

@media screen and(max-width:750px) {
	.dan {
		&_flow {
			li {
				margin: 0 4px;
			}

			&_item {
				padding: 0 12px;
				height: 32px;
				line-height: 32px;
			}
		}
		&_form {
			padding: 12px;
			&_wrap {
				padding: 12px 0;
			}
		}
		&_file {
			&_txt {
				top: 136px;
				right: 50%;
				transform: translateX(50%);
			}
			&_name {
				top: 106px;
				left: 50%;
				transform: translateX(-50%);
			}
		}
		&_level {
			padding: 12px 0;
		}
		&_success {
			padding-top: 30px;
		}
	}
}