.brand {
	&_box {
		position: relative;
		height: 580px;
		padding-top: 110px;
		overflow: hidden;
		@include bg('../img/brand_bg.png',cover);
	}
	&_txt {
		width: 1080px;
		font-size: 20px;
		margin: 0 auto;
		@include rowhide(4,54px);
	}
	&_num {
		font-size: 48px;
		font-weight: bold;
	}
	&_title {
		font-size: 20px;
	}
}

@media screen and(max-width:750px) {
	.brand {
		&_box {
			height: auto;
			padding: 12px;
		}
		&_txt {
			width: auto;
			height: auto;
			font-size: 16px;
			line-height: 30px;
			-webkit-line-clamp: inherit;
		}
		&_num {
			font-size: 30px;
		}
		&_title {
			font-size: 16px;
		}
		&_item {
			margin-bottom: 16px;
		}
	}
}