.solve {
	&_wrap {
		padding: 20px 0;
	}

	&_left {
		width: 800px;
	}

	&_time {
		width: 360px;
	}

	&_date {
		position: relative;
		@include size(288px,344px);
		margin: 0 auto 24px;
		background: url(../img/solve_2_bg.png) no-repeat center;
	}

	&_right {
		position: relative;
		@include size(350px, 312px);
		margin-top: 30px;
		background-color: $bg-color;
	}

	&_pic {
		position: absolute;

		&.one {
			left: 20px;
			top: -20px;
		}
		&.two {
			top: 24px;
			left: 24px;
			z-index: -1;
		}

	}

	&_hd {
		margin-bottom: 30px;
		margin-top: 30px;
		line-height: 46px;

		span {
			display: inline-block;
			margin-right: 20px;
		}
	}

	&_tips {
		@include size(46px, 46px);
		line-height: 46px;
		background: url(../img/solve_title_bg.png) no-repeat center;
		color: $white-color;
	}

	&_bd {
		@include rowhide(5, 26px);
		text-align: justify;
	}
}

@media screen and(max-width:750px) {
	.solve {
		&_wrap {
			padding: 12px;
		}

		&_hd {
			margin-bottom: 12px;
		}
		&_date {
			
		}
	}
}