.lab {
	&_reset {
		position: absolute;
		right: 12px;
		top: 50%;
		transform: translateY(-50%);
	}
	&_cell {
		margin-bottom: 30px;
		background-color: $bg-color;
	}
	&_pic {
		overflow: hidden;
		@include size(290px,290px);
	}
	&_txt {
		@include size(266px,290px);
		padding: 20px 30px;		
	}
	&_item {
		&:nth-last-child(1) {
			float: left!important;
		}
	}
	&_title {
		position: relative;
		margin-bottom: 16px;
		font-size: 20px;
		color: #f00;
		span {
			margin-right: 20px;
			font-weight: bold;
			font-size: 20px;
			color: #000;
		}
		&:after {
			content: '';
			position: absolute;
			left: 0;
			bottom: -10px;
			@include size(150px,2px);
			background-color: $main-color;
		}
	}
	&_cont {
		@include rowhide(7,20px);
		margin: 8px 0;
		text-align: justify;
	}
	&_btn {
		a {
			display: inline-block;
			height: 24px;
			line-height: 22px;
			padding: 0 12px;
			border: 1px solid $main-color;
			color: $main-color;
			transition: $easein;
			&:hover {
				@include colbg($white-color,$main-color);
			}
		}
	}
	&_unit {
		line-height: 30px;
		padding: 10px 20px;
		border-bottom: $common-border;
		&:nth-last-child(1) {
			border-bottom: none;
		}
		&_title {
			margin-right: 20px;
		}
		&_list {
			width: 1020px;
			height: 30px;
			margin-right: 20px;
			overflow: hidden;
			&.active {
				height: auto;
			}
			li {
				display: inline-block;
				margin-right: 20px;
				&.active,
				&:hover {
					color: $main-color;
				}
			}
		}
	}
	&_more {
		cursor: pointer;
		color: #8c8c8c;
		&.active {
			img {
				transform: rotate(180deg);
			}
		}
		img {
			vertical-align: middle;
			margin-left: 8px;
		}
	}
}

@media screen and(max-width:750px) {
	.lab {
		&_cell {
			margin-bottom: 12px;
		}
		&_unit_title {
			width: 100%;
		}
		&_unit_list {
			width: 100%;
			height: auto;
		}
		&_more {
			display: none;
		}
	}
}