.sider {
	&_box {
		position: fixed;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		width: 160px;
		overflow: hidden;
		text-align: center;
		font-size: 18px;
		border-radius: 8px;
		background-color: $bg-color;
		z-index: 999;
		p {
			@include lineht(40px);
		}
	}
	&_top,
	&_btm {
		@include lineht(64px);
		background-color: $main-color;
		color: $white-color;
		i {
			margin-right: 4px;
		}
	}
	&_icon {
		margin-top: 12px;
	}
	&_phone {
		padding: 0 20px;
		h6 {
			margin-top: 20px;
			padding-left: 20px;
			height: 32px;
			line-height: 32px;
			background: url(../img/phone_bg.png) no-repeat left center;
			background-size: contain;
		}		
	}
	&_contact {
		border-top: 1px solid $main-color;
	}
	&_close {
		position: absolute;
		top: 10px;
		right: 10px;
		cursor: pointer;
		i {
			color: $white-color;
		}
	}
}

@media screen and (max-width:750px) {
	.sider {
		&_box {
			display: none;
		}
	}
}