.success {
	&_left {
		display: block;
		@include size(550px,422px);
		@extend .bg_cover;
	}
	&_right {
		@include size(640px,422px);
	}
	&_item {	
		@include size(315px,206px);
		padding: 0;	
		&:nth-child(odd) {
			margin-right: 10px;
		}
		&:nth-child(n-3) {
			margin-bottom: 10px;
		}
	}
	&_cell {
		display: block;
		@include size(315px,206px);
		@extend .bg_cover;
	}
	&_link {
		margin-top: 30px;
		&_item {
			display: block;
		}
		&_pic {
			display: block;
			margin-bottom: 12px;
			width: 280px;
			height: 106px;
			border: 1px solid $border-color;
		}
	}
}

@media screen and(max-width:750px) {
	.success {
		&_wrap {
			padding: 12px;
		}
		&_left,
		&_right {
			width: 100%;
		}
		&_right {
			height: auto;
		}
		&_left {
			height: 300px;
		}
		&_item {
			width: 50%;
			height: auto;
			margin: 0!important;
		}
		&_item:nth-child(odd) {
			text-align: left;
		}
		&_item:nth-child(even) {
			text-align: right;
		}
		&_cell {
			margin-top: 4px;
			display: inline-block;
			width: 98%;
			height: 130px;
		}
		&_link {
			margin-top: 0;
		}
		&_link_pic {
			width: 160px;
			height: 60px;
			margin-bottom: 8px;
		}
	}
}