.crumb {
	&_box {
		height: 80px;
		border-bottom: $common-border;
	}

	&_search {
		padding-top: 22px;
	}

	&_icon {
		display: block;
		float: left;
		@include size(80px, 80px);
		line-height: 80px;
		text-align: center;
		background-color: $bg-color;

		i {
			font-size: 42px;
			color: $main-color;
		}
	}

	&_list {
		float: left;
		line-height: 80px;
		margin-left: 30px;
		font-size: 18px;

		a,
		i {
			margin: 0 6px;
		}
	}

	&_form {
		.ipt {
			width: 300px;
			height: 36px;
			line-height: 34px;
			text-indent: 10px;
			border: $common-border;
			border-right: none;
		}

		.search {
			width: 100px;
			height: 36px;
			text-align: center;
			color: #fff;
			background-color: $main-color;
		}
	}
}

@media screen and (max-width:750px) {
	.crumb {
		&_box {
			height: 40px;
		}

		&_search {
			display: none;
		}

		&_icon {
			@include size(40px, 40px);
			line-height: 40px;

			i {
				font-size: 20px;
			}
		}

		&_list {
			line-height: 40px;
			margin-left: 12px;
			font-size: 15px;
		}
	}
}