.user {
	&_menu {
		width: 280px;
		padding-top: 12px;
		margin-bottom: 12px;
		font-size: 18px;
		&_cell {
			padding: 20px 0;
			img {
				margin-right: 20px;
			}
		}
		&_item {
			position: relative;
			display: block;
			@include lineht(90px);
			border-bottom: $common-border;
			&:nth-last-child(1) {
				border-bottom: none;
			}
			&:before {
				content: '-';
				display: inline-block;
				color: $main-color;
				margin-right: 6px;
			}
			&.active,
			&:hover {
				color: $main-color;
			}
		}
	}
	&_show {
		width: 880px;
		margin-bottom: 20px;
		overflow: hidden;
	}
	&_title {
		position: relative;
		padding-left: 20px;
		@include lineht(50px);
		&:before {
			content: '';
			@include position(10px,17px);
			display: block;
			@include size(3px,16px);
			background-color: $main-color;
			border-radius: 4px;
		}
	}
	&_wrap {
		line-height: 30px;
	}
	&_price {
		padding: 30px 20px 30px 0;
		line-height: 30px;
		&_red {
			font-weight: bold;
			color: #f00;
		}
	}
	&_icon {
		margin-right: 20px;
	}
	&_dope {
		line-height: 24px;
		&_item {
			padding-bottom: 12px;
			margin-bottom: 12px;
			&:nth-last-child(1) {
				margin-bottom: 0;
			}
		}
		&_title {
			position: relative;
			&.active:after {
				content: '';
				display: inline-block;
				font-weight: bold;
				color: #f00;
				position: relative;
				top: -10px;
				left: 6px;
			}
		}
		&_date {
			color: $nine-color;
		}
	}
}

@media screen and(max-width:750px) {
	.user {
		&_menu {
			padding: 0;
			font-size: 16px;
			&_cell {
				padding: 10px 0;
			}
			&_item {
				height: 50px;
				line-height: 50px;
				border-bottom: none;
				border-right: $common-border;
				&:nth-last-child(1) {
					border-right: none;
				}
			}
		}
		&_price {
			padding: 12px;
			&_num {
				text-align: left!important;
			}
		}
	}
}